                max-width : 100%;
                left      : 0;
              }

              .question {

                .left-button {
                  margin-top : 0em;
                  img {
                    &.plus-icon {
                      position : relative;
                      top      : 3px;
                      width    : 23px;
                    }
                  }

                  @media (max-width : 1200px) {
                    margin-top : 0;
                  }
                  /*QUESTION P STYLE*/
                  p.title-question {
                    color       : #1E3977;
                    font-weight : bold;
                    .margin(0, 0, 10px, 10px);
                    user-select : none;
                    @media (max-width : 480px) {
                    }
                  }
                }
                .answer {

                  /*ANSWER TEXT*/
                  .answer-text {
                    border        : 1px dotted gray;
                    background    : white;
                    padding       : 0.5em;
                    margin-bottom : 0.5em;
                    @media (max-width : 480px) {
                      padding    : 0.5em;
                      text-align : justify;
                    }
                  }
                }
              }
            }
          }

          /*LIVE THIRD PARTY*/
          &#live-third-party {

            @media (max-width : 600px) {
              width : 100%;
            }
            .row {
              margin : 0 auto;
            }

            max-width : 928px;

            .table-trader {
              background : white;
              padding    : 2em;
              box-shadow : 1px 1px 6px 1px #B5B5B5;
              margin     : 1.5em 0;
              @media (max-width : 500px) {
                padding : 0.5em;
              }
              ul {
                list-style : none;
                padding    : 0;
                li {
                  color       : black;
                  font-weight : bold;
                  font-size   : 1.1em;
                  border      : 1px solid white;
                  border-top  : 4px #0086C6;

                  @media (max-width : 500px) {
                    font-size : 0.9em;
                    direction : rtl;

                  }
                }
              }

              #trades {
                .line-trades {
                  background    : white;
                  padding-left  : 0;
                  padding-right : 0;
                  &:nth-of-type(odd) {
                    background : #D8D8D8;
                    &:hover {
                      background : gray;
                    }
                  }

                  .trades {
                    &.trade-profit {
                      font-weight : 600;
                    }
                    .first-word {
                      font-weight : bold;
                    }

                    @media (max-width : 1347px) {
                      min-height : 50px;
                    }
                    @media (max-width : 480px) {
                      min-height : 10vh;
                    }
                    padding : 0.2em;
                    border  : 1px solid white;
                  }
                }
                @media (max-width : 480px) {
                  font-size : 0.7em;
                }
              }
            }
          }
          /*LIVE FACEBOOK FEEDS*/
          &#live-result-pic {
            @media (max-width : 560px) {
              box-shadow : 1px 1px 31px 11.4px lightgray;
              width      : 100%;
            }

            .social {
              box-shadow     : -1px 3px 25px 8px #D3D3D1;
              background     : #F0F0F0;
              padding-bottom : 2em;
              display        : block;
              margin         : 0 10em;
              width          : 76%;

              @media (max-width : 1199px) {
                margin : 0 auto;
                width  : 100%;
              }
              @media (max-width : 1010px) {
                margin : 0 auto;
                width  : 100%;
              }

              #content-social {

                header {
                  border : 1px solid lightgray;
                }
                /*FACEBOOK TAB CONTENT*/
                #facebook-tab {
                  background : white;
                  border     : 1px solid lightgray;
                  padding    : 0;
                  min-height : 15em;

                  .facebook-message {
                    height     : 167.2px;
                    background : white;
                    border     : 1px solid gray;

                    /*FACEBOOK IMAGE NAME AND TIME*/
                    .fb-img-name-time {
                      .fb-img {
                        padding : 0.5em;

                        /*IMAGE TIME*/
                        .fb-human-image {
                          border-radius : 11px;
                        }
                      }

                      /*NAME AND TIME IN FACEBOOK*/
                      .fb-name-n-time {
                        color     : #3B5997;
                        font-size : 1.4em;
                        /*TIME*/
                        .ctime {
                          color : #8B9396;
                        }
                        /*NAME*/
                        .fb-name {
                          color : inherit;
                        }
                      }

                      /*BOTTOM CONTENT (MESSAGE)*/
                      .bottom {
                        .fb-tab {
                          color : #3B5997;
                        }
                      }
                    }
                    /*PLACE FOR CHAT (COMMENT IN THE BOTTOM*/
                  }
                }

                /*TWEETS TAB CONTENT*/
                #tweets-tab {

                  .tweet-message {
                    /*DIV OF THE FACEBOOK MESSAGE*/

                    .twt-img-name-time {
                      .twt-image {
                        border-radius  : 0.3em;
                        vertical-align : top;
                        img {
                          /*TWEET IMAGE*/
                        }
                        .twt-name-n-time {
                          &.twtname {
                            color        : black;
                            padding-left : 0.2em;
                          }
                        }
                      }
                      .feed_name2 {
                        //min-height: 14.3em;
                      }
                      .ctime2 {
                        float : right;
                      }
                      /*TWEET DATA*/
                      .twt-data {
                        display        : inline-block;
                        vertical-align : middle;
                        width          : 100%;
                        padding        : 0 0.6em;
                        height         : 7.9em;
                        text-align     : justify;
                      }
                    }
                  }
                }
              }
            }
            /*TWEETER AND FACEBOOK TAG (DIVS)*/
            div.feed_name2, div.feed_name1 {
              border        : 1px solid gray;
              padding       : 4px;
              margin-bottom : 0em;
              text-align    : left;
              background    : white;
            }
            .images {
              .logotag {
                padding : 1.4em 0;
              }
            }
          }
        }
      }
    }

    /*BACK TO TOP ICON*/
    a#back-to-top {
      position        : fixed;
      bottom          : 98px;
      right           : 40px;
      z-index         : 9999;
      width           : 60px;
      height          : 60px;
      text-align      : center;
      line-height     : 30px;
      background      : #f5f5f5;
      color           : #444444;
      cursor          : pointer;
      border          : 0;
      border-radius   : 2px;
      text-decoration : none;
      transition      : opacity 0.2s ease-out;
      opacity         : 0;
      font-size       : 50px;
      padding-top     : 10px;
      font-weight     : bolder;
    }

    /*BACK TO TOP SHOW */
    #back-to-top {
      &.show {
        opacity : 1;
      }
      &:hover {
        background : #e9ebec;
      }
    }

    /*FOOTER ROW*/
    footer.row {
      background-color : #1F3A79;
      color            : white;
      margin           : 0;
      padding          : 1em;
    }
  }
  .loading {
    width       : 100%;
    background  : white;
    z-index     : 19999;
    position    : absolute;
    top         : 0px;
    height      : 387px;
    padding-top : 9em;
    border      : 4px solid #1F3A79;
  }

}

